import React from 'react';
import { BrowserRouter as Router, Routes, Route } from 'react-router-dom';

function Home() {
  return <h1>Hi, this is the home page</h1>;
}
function Board() {
  return <h1>Hi, this is the board page</h1>;
}
function Lists() {
  return <h1>Hi, this is the lists page</h1>;
}
function App() {
  return (
    <Router>
      <Routes>
        <Route path="/" element={<Home />} />
        <Route path="/board" element={<Board />} />
        <Route path="/lists" element={<Lists />} />
      </Routes>
    </Router>
  );
}
export default App;